/* FoodItems.css */

/* Root styling for flexibility */
:root {
    --primary-color: #ff6347; /* Tomato */
    --secondary-color: #fff;
    --accent-color: #f4f4f4;
    --text-color: #333;
    --border-radius: 8px;
    --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  /* body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    color: var(--text-color);
    background-color: var(--accent-color);
  } */
  
  .food-items {
    display: flex;
    flex-direction: column;
    background: var(--secondary-color);
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    overflow: hidden;
    margin: 15px;
    transition: transform 0.3s ease;
  }
  
  .food-items:hover {
    transform: translateY(-5px);
  }
  
  .food-item-image-container {
    position: relative;
    text-align: center;
    background-color: var(--accent-color);
  }
  
  .food-item-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-bottom: 1px solid #eee;
  }
  
  .remove-icon, .add-icon {
    cursor: pointer;
    fill: var(--primary-color);
    transition: fill 0.3s ease;
  }
  .add1 {
    cursor: pointer;
    fill: var(--primary-color);
    transition: fill 0.3s ease;
    position: absolute;
    top: 10px;
    left: 10px;
    background: #f8f8f8;
    width: 33px;
    height: 38px;
    /* padding: 8px 10px; */
    border-radius: 4px;
  }
  
  .add1:hover, .remove-icon:hover, .add-icon:hover {
    fill: #d9534f;
  }
  
  .food-item-counter {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #f8f8f8;
    padding: 8px 10px;
    border-radius: 4px;
    position: absolute;
    top: 10px;
    left: 10px;
  }
  
  .food-item-counter p {
    margin: 0 10px;
    font-size: 16px;
    font-weight: bold;
  }
  
  .food-item-info {
    padding: 15px;
  }
  
  .food-item-name-rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .food-item-name-rating p {
    font-size: 18px;
    font-weight: bold;
  }
  
  .food-item-price {
    color: var(--primary-color);
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .food-item-price del {
    color: #999;
    margin-right: 5px;
  }
  
  .food-item-category {
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
  }
  
  .food-item-category a {
    color: var(--primary-color);
    text-decoration: none;
    font-weight: bold;
  }
  
  .food-item-category a:hover {
    text-decoration: underline;
  }
  
  .invoicebtns {
    display: flex;
    gap: 10px;
    margin-top: 10px;
  }
  
  .buy-btn {
    background: var(--primary-color);
    color: var(--secondary-color);
    border: none;
    padding: 10px 20px;
    border-radius: var(--border-radius);
    font-size: 16px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .buy-btn:hover {
    background: #e5533e;
  }
  
  /* Responsive design */
  @media (max-width: 768px) {
    .food-items {
      margin: 10px auto;
      width: 90%;
    }
  
    .food-item-counter {
      top: 5px;
      left: 5px;
    }
  
    .food-item-info {
      padding: 10px;
    }
  
    .buy-btn {
      padding: 8px 16px;
      font-size: 14px;
    }
  }
  
  @media (max-width: 480px) {
    .food-items {
      margin: 10px 0 10px 30px;
    }
  
    .food-item-image {
      height: 180px;
      object-fit: cover;
    }
  
    .food-item-info {
      text-align: center;
    }
  
    .invoicebtns {
      flex-direction: column;
      gap: 5px;
    }
  }
  