/* LoginPopup.css */

.login-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.login-popup-container {
    background-color: #fff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 400px;
    max-width: 90%;
    transition: all 0.3s ease-in-out;
}

.login-popup-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
}

.login-popup-title h2 {
    margin: 0;
    font-size: 1.5rem;
    color: #333;
}

.login-popup-title img {
    cursor: pointer;
    width: 20px;
    height: 20px;
}

.login-popup-inputs {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 1.5rem;
}

.login-popup-inputs input {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
}

.login-popup-inputs input:focus {
    outline: none;
    border-color: #007bff; /* Add a color for focus state */
    box-shadow: 0px 0px 4px rgba(0, 123, 255, 0.5);
}

.login-popup button {
    width: 100%;
    padding: 0.75rem;
    background-color: black;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-popup button:hover {
    background-color: rgb(37, 36, 36);
}

.login-popup-condition {
    display: flex;
    align-items: center;
    margin-top: 1rem;
    font-size: 0.9rem;
    color: #666;
}

.login-popup-condition input {
    margin-right: 0.5rem;
    cursor: pointer;
}

.login-popup-condition p {
    margin: 0;
}

.login-popup-condition p span {
    color: #007bff;
    cursor: pointer;
    text-decoration: underline;
}

.login-popup-condition p span:hover {
    color: #0056b3;
}

.login-popup p {
    text-align: center;
    margin-top: 1.5rem;
    font-size: 0.9rem;
    color: #666;
}

.login-popup p span {
    color: #007bff;
    cursor: pointer;
    text-decoration: underline;
}

.login-popup p span:hover {
    color: #0056b3;
}

/* Responsive Design */
@media (max-width: 768px) {
    .login-popup-container {
        width: 90%;
        padding: 1.5rem;
    }

    .login-popup-title h2 {
        font-size: 1.3rem;
    }

    .login-popup-inputs input {
        font-size: 0.9rem;
        padding: 0.6rem;
    }

    .login-popup button {
        font-size: 0.9rem;
        padding: 0.6rem;
    }

    .login-popup-condition {
        font-size: 0.8rem;
    }

    .login-popup p {
        font-size: 0.8rem;
    }
}

@media (max-width: 480px) {
    .login-popup-container {
        padding: 1rem;
    }

    .login-popup-title h2 {
        font-size: 1.2rem;
    }

    .login-popup-inputs input {
        font-size: 0.8rem;
        padding: 0.5rem;
    }

    .login-popup button {
        font-size: 0.8rem;
        padding: 0.5rem;
    }

    .login-popup-condition {
        font-size: 0.7rem;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }

    .login-popup-condition p {
        margin-top: 0.5rem;
    }

    .login-popup p {
        font-size: 0.7rem;
    }
}
