  .privacy-policy-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .privacy-policy-container h1 {
    color: #d9534f;
    font-size: 2.5rem;
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 2px solid #d9534f;
  }
  
  .privacy-policy-container p {
    font-size: 1rem;
    color: #555;
    line-height: 1.6;
    margin: 10px 0;
  }
  
  .privacy-policy-container ul {
    list-style-type: disc;
    margin-left: 20px;
    color: #555;
  }
  
  .privacy-policy-container ul li {
    margin-bottom: 10px;
  }
  
  .privacy-policy-container section {
    margin-bottom: 30px;
  }
  
  .privacy-policy-container h2 {
    color: #d9534f;
    font-size: 1.5rem;
    margin-top: 20px;
    padding-bottom: 5px;
    border-bottom: 1px solid #d9534f;
  }
  
  .privacy-policy-container footer {
    text-align: center;
    margin-top: 30px;
    font-size: 0.9rem;
    color: #aaa;
  }
  
  /* Adding food-themed background image */
  .privacy-policy-container {
    /* background-image: url('https://via.placeholder.com/800x400.png?text=Delicious+Food'); */
    /* background-size: cover; */
    /* background-position: center; */
    background-color: rgb(83, 76, 76);
    color: #fff;
    padding: 40px;
  }
  
  .privacy-policy-container h1,
  .privacy-policy-container h2,
  .privacy-policy-container p,
  .privacy-policy-container ul {
    color: #fff;
  }
  
  .privacy-policy-container footer {
    font-size: 0.8rem;
    color: #fff;
  }
  
  /* Button Style for actions (optional) */
  .privacy-policy-container button {
    background-color: #d9534f;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
  }
  
  .privacy-policy-container button:hover {
    background-color: #c9302c;
  }
  