/* Base Styling */
.cart {
  margin-top: 100px;
  padding: 20px;
  font-family: Arial, sans-serif;
}
.cart .title
{
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 10px;
}
.cart-items {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.cart-items th, .cart-items td {
  border: 1px solid #b4afaf;
  padding: 10px;
  text-align: center;
  vertical-align: middle;
}

.cart-items th {
  background-color: #f8f8f8;
  font-weight: bold;
}

.cart-items img {
  width: 50px;
  height: auto;
  border-radius: 5px;
}

.cart-items-item {
  background-color: #ffffff;
}

.cart-items-item:hover {
  background-color: #f9f9f9;
}

.cart svg {
  cursor: pointer;
}

.cart-bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  gap: 20px;
}

.cart-promocode {
  flex: 1;
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
}

.cart-promocode-input {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.cart-promocode-input input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.cart-promocode-input button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #ffffff;
  cursor: pointer;
}

.cart-promocode-input button:hover {
  background-color: #0056b3;
}

.cart-total {
  flex: 1;
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
}

.cart-total h2 {
  margin-bottom: 20px;
}

.cart-total p {
  margin: 5px 0;
  font-size: 16px;
}

.cart-total button {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #28a745;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
}

.cart-total button:hover {
  background-color: #218838;
}

/* Responsive Design */
@media (max-width: 768px) {
  .cart-items th, .cart-items td {
    font-size: 14px;
    padding: 8px;
  }

  .cart-bottom {
    flex-direction: column;
  }

  .cart-total,
  .cart-promocode {
    margin-bottom: 20px;
  }
}

@media (max-width: 480px) {
  .cart {
    padding: 10px;
  }

  .cart-items th, .cart-items td {
    font-size: 12px;
    padding: 5px;
  }

  .cart-promocode-input input {
    font-size: 12px;
  }

  .cart-promocode-input button,
  .cart-total button {
    font-size: 14px;
  }
}
