.explore-menu-navigation
{
    display: flex;
    justify-content: center;
    gap: 20px;
}
.explore-menu
{
    display: flex;
    flex-direction: column;
    gap: 20px
}
.explore-menu-text
{
    max-width: 60%;
}
.explore-menu-list
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 20px 0px;
    overflow-x: scroll;
    scrollbar-width: none;
}
.explore-menu-list-item img
{
    width: 144px;
    min-width: 14px;
    height: 180px;
    min-height: 180px;
    border-radius: 50px;
    transition: 0.2s;
    cursor: pointer;
}
.fdimg
{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.explore-menu hr
{
    margin: 10px 0px;
    height: 2px;
    border: none;
}
.explore-menu-list-item .active
{
    border: 2px solid black;
    padding: 2px;
}