  .App {
    max-width: 1200px;
    margin: auto;
    padding: 20px;
  }
  
  /* Title Styling */
  .App h1 {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #333;
  }
  
  /* Menu Container */
  .menu {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  /* Individual Menu Item */
  .menu-item {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    width: calc(100% / 3 - 40px);
    max-width: 300px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .menu-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.5);
  }
  
  /* Menu Item Image */
  .menu-item img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  /* Menu Item Content */
  .menu-item h2 {
    font-size: 1.5rem;
    margin: 10px 15px 5px;
    color: #555;
  }
  
  .menu-item p {
    font-size: 0.9rem;
    margin: 5px 15px;
    color: #666;
    line-height: 1.5;
  }
  
  /* Highlight strong elements */
  .menu-item p strong {
    color: #333;
  }
  
  /* Responsive Design */
  @media (max-width: 1024px) {
    .menu-item {
      width: calc(100% / 2 - 40px);
    }
  }
  
  @media (max-width: 768px) {
    .menu-item {
      width: 100%;
    }
  }
  