.header
{
    height: 34vw;
    margin: 30px auto;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(./Food-Logo.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
}
.header-contents
{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 1.5vw;
    max-width: 50%;
    bottom: 10%;
    left: 6vw;
    animation: fadeIn 3s;
}
.header-contents h2
{
    font-weight: 500;
    color: white;
    font-size: max(4.5vw, 22px);
}
.header-contents p
{
    color: white;
    font-size: 1.2vw;
}
.header-contents button
{
    border: none;
    font-weight: 400;
    padding: 0.7vw 2.1vw;
    background-color: white;
    font-size: max(1.2vw, 13px);
    border-radius: 50px;
}
