/* General Styling for My Orders Section */
.my-orders {
    margin: 50px auto;
    width: 90%;
    max-width: 1200px;
    font-family: 'Arial', sans-serif;
    color: #333;
}

.my-orders h1 {
    text-align: center;
    font-size: 2.5rem;
    color: #4caf50;
    margin-bottom: 20px;
}

/* Container for Orders */
.my-orders .container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 30px;
}

/* Individual Order Card */
.my-orders-order {
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 2fr 1fr;
    align-items: center;
    gap: 20px;
    font-size: 14px;
    padding: 15px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
}

.my-orders-order:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Image Styling */
.my-orders-order img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 2px solid #4caf50;
    object-fit: cover;
}

/* Order Details */
.my-orders-order p {
    margin: 0;
    font-size: 14px;
    color: #555;
    text-align: left;
}

.my-orders-order p span {
    color: tomato;
    font-size: 16px;
    font-weight: bold;
}

.my-orders-order p b {
    color: #4caf50;
    font-weight: bold;
}

/* Button Styling */
.my-orders-order button {
    padding: 8px 12px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.my-orders-order button:hover {
    background-color: #45a049;
}

/* Responsive Design */
@media (max-width: 768px) {
    .my-orders .container {
        gap: 15px;
    }

    .my-orders-order {
        grid-template-columns: 0.7fr 2.5fr 1fr 1fr 1.5fr;
        font-size: 12px;
        padding: 10px;
    }

    .my-orders-order img {
        width: 40px;
        height: 40px;
    }

    .my-orders-order button {
        font-size: 12px;
        padding: 6px 10px;
    }
}

@media (max-width: 480px) {
    .my-orders-order {
        grid-template-columns: 1fr 2fr;
        grid-template-areas: 
            "img details"
            "status track";
        text-align: center;
        gap: 10px;
    }

    .my-orders-order img {
        grid-area: img;
        margin: auto;
    }

    .my-orders-order p {
        grid-area: details;
    }

    .my-orders-order button {
        grid-area: track;
    }

    .my-orders-order p span {
        font-size: 14px;
    }
}
